export function inventoryCapacityFilter(soldQty, inventory, infinity) {
  if (infinity) return `${soldQty} / `
  const noInventory = soldQty === 0 && inventory === 0
  const eventIsFull = soldQty >= inventory

  return noInventory ? '' : eventIsFull ? '' : `${soldQty} / ${inventory}`
}
export function inventoryRepeatFilter(type, withArray) {
  const repeatTypeMap = {
    day: '',
    week: `${repeatWithWeek(withArray)}`,
  }
  return repeatTypeMap[type] || ''
}
export const daysInWeek = ['', '', '', '', '', '', '']

function repeatWithWeek(daysArray) {
  // * sort() will effect original array
  const sortedDaysArray = Object.assign([], daysArray)
    .sort()
    .map((day) => daysInWeek[day])

  const numberOfDays = daysArray.length
  return numberOfDays === daysInWeek.length
    ? ''
    : numberOfDays <= 2
    ? sortedDaysArray.join('')
    : `${sortedDaysArray.slice(0, -1).join('')}${sortedDaysArray.slice(-1)}`
}

